<template lang="html">
  <div id="billManage">
      <div class="billManage-head clearfix">
          <div class="head-left clearfix">
            <h2>发票管理</h2>
            <span>/</span>
            <span>说明说明说明说明说明</span>
          </div>
          <div class="head-right">
              EXCEL导出
          </div>
      </div>
      <div class="billManage-memu">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
                      <el-form-item label="受票方">
                          <el-input v-model="formInline.user" placeholder="请输入订单号" style=""></el-input>
                      </el-form-item>
                      <el-form-item label="开票状态">
                          <el-select v-model="formInline.region" placeholder="选择下单区间" style="">
                              <el-option label="区域一" value="shanghai"></el-option>
                              <el-option label="区域二" value="beijing"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="申请时间">
                         <el-col :span="25">
                             <el-date-picker type="date" placeholder="选择" v-model="form.date1" style=""></el-date-picker>
                         </el-col>
                      </el-form-item>
                      <br />
                      <el-form-item label="发票代码">
                          <el-select v-model="formInline.regione" placeholder="选择区间" style="">
                              <el-option label="区域一" value="shanghai"></el-option>
                              <el-option label="区域二" value="beijing"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="运单号">
                          <el-select v-model="formInline.regiont" placeholder="选择" style="">
                              <el-option label="区域一" value="shanghai"></el-option>
                              <el-option label="区域二" value="beijing"></el-option>
                          </el-select>
                      </el-form-item>
                      <el-form-item label="开票时间">
                         <el-col :span="25">
                             <el-date-picker type="date" placeholder="选择" v-model="form.date1" style=""></el-date-picker>
                         </el-col>
                      </el-form-item>
                      <br/>
                      <el-form-item>
                       <el-button type="primary" @click="onSubmit" style="">查询</el-button>
                       </el-form-item>
                      </el-form>

      </div>

      <div class="table-head clearfix">
        <div class="table-headLeft">
          <p>
            <span>未通过:</span>
            <span class="price-o price">0.00</span>
            <span>元</span>
          </p>
          <p>
            <span class="left">申请中:</span>
            <span class="price-t price">0.00</span>
            <span>元</span>
          </p>
          <p>
            <span class="left">已开票:</span>
            <span class="price-th price">0.00</span>
            <span>元</span>
          </p>

        </div>
        <div class="table-headRight">
          <span class="left">合计:</span>
          <span class="price-f price">0.00</span>
          <span>元</span>
        </div>
      </div>

      <div class="billManage-message">
                        <el-table
                ref="multipleTable"
                :data="tableData3"
                border
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange">
                <el-table-column
                label="发票代码"
                width="140">
                <template scope="scope">{{ scope.row.coding}}</template>
                </el-table-column>
                <el-table-column
                prop="name"
                label="客户（受票方）"
                width="210">
                </el-table-column>
                <el-table-column
                prop="sum"
                label="开票额"
                width="150">
                </el-table-column>
                <el-table-column
                prop="state"
                label="开票状态"
                width="150">
                </el-table-column>

                <el-table-column
                prop="Sdate"
                label="申请时间"
                width="150">
                </el-table-column>
                <el-table-column
                prop="Kdate"
                label="开票时间"
                width="170">
                </el-table-column>
                <el-table-column
                prop="price"
                label="实付运费"
                width="170">
                </el-table-column>
                <!-- <router-link tag="el-table-column"
                to="/"
                  prop="message"
                  label="开票信息"
                  width="150">

                </router-link> -->
                <el-table-column
                label="开票信息"
                width="160">
                  <template scope='scope'>
                      <router-link to='la' class="see">
                    {{scope.row.message}}
                    </router-link>
                    </template>
                </el-table-column>
                <router-view></router-view>
                <el-table-column
                prop="comment"
                label="备注"
                width="240">
                </el-table-column>
                </el-table>
      </div>


  </div>
</template>

<script>
export default {
  data() {
    return {
      formInline: {
        user: '',
        region: '',
        regione: '',
        regiont: '',
        regionh: '',
        regionf: '',
        regioni: '',
        regionb: ''
      },
      form: {
        date1: '',
        date2: ''
      },
      tableData3: [{
        coding: '00453391',
        name: '安东石油技术（集团）有限公司',
        address: '上海市普陀区金沙江路 1518 弄',
        sum: '228900,00',
        state:'已开票',
        Sdate:'2016-07-26',
        Kdate:'2016-08-26',
        price:'176990.00',
        message:'查看',
        comment:'客户要求票面显示车主'
      }, {
        coding: '00453391',
        name: '安东石油技术（集团）有限公司',
        address: '上海市普陀区金沙江路 1518 弄',
        sum: '228900,00',
        state:'已开票',
        Sdate:'2016-07-26',
        Kdate:'2016-08-26',
        price:'176990.00',
        message:'查看',
