        sum: '228900,00',
        state:'已开票',
        Sdate:'2016-07-26',
        Kdate:'2016-08-26',
        price:'176990.00',
        message:'查看',
        comment:'客户要求票面显示车主'
      }, {
        coding: '00453391',
        name: '安东石油技术（集团）有限公司',
        address: '上海市普陀区金沙江路 1518 弄',
        sum: '228900,00',
        state:'已开票',
        Sdate:'2016-07-26',
        Kdate:'2016-08-26',
        price:'176990.00',
        message:'查看',
        comment:'客户要求票面显示车主'
      }],
      multipleSelection: [],
      methods: {
        toggleSelection(rows) {
          if (rows) {
            rows.forEach(row => {
              this.$refs.multipleTable.toggleRowSelection(row);
            });
          } else {
            this.$refs.multipleTable.clearSelection();
          }
        },
        handleSelectionChange(val) {
          this.multipleSelection = val;
        }
      }
    }
  }
}
</script>

<style lang="less">
@import "../../static/base.css";
#billManage {
    padding: 0 30px;
    margin: 0 auto;
    background:#FAFBFF;
    width: 1600px;
    // 头部
    .billManage-head {
        border-bottom: 1px solid #e7e8ed;
        padding: 26px 0;
        // padding: 36px 0;
        .head-left {
            float: left;
            margin: 10px 0;
            h2 {
                float: left;
                color: #384eb3;
                font-size: 20px;
            }
            span {
                float: left;
                font-size: 14px;
                color: #899ab4;
                margin-top: 3px;
            }
            span:nth-of-type(1) {
                margin: 0 10px;
            }
        }
        .clearfix:after {
            clear: both;
            content: " ";
            display: block;
        }
        .head-right {
            float: right;
            width: 100px;
            height: 40px;
            border-radius: 5px;
            text-align: center;
            line-height: 40px;
            color: #367d37;
            border: 1px #367d37 solid;
        }
    }
    .clearfix:after {
        clear: both;
        content: " ";
        display: block;
    }
    .table-head{
      padding:10px 0;
      margin:10px 0;
      border-top:1px solid #f5efef;
      border-bottom:1px solid #f5efef;
      background:#FFFCFD;
      .table-headLeft{
        float:left;
        p{
          float:left;
          span:nth-of-type(3){
            margin-right:10px;
          }
          .price{
            color:red;
          }
        }

      }
      .table-headRight{
        float:right;
        margin-right:50px;
        .price{
          color:red;
        }
      }
    }
    .billManage-memu {
      width: 100%;
      background: #fff;
      padding: 40px 0px 30px 0;
        .el-input {
          width:220px;
            input {
                width: 225px;
                height: 40px;
                background-color: #f5f6fb;
            }
            input::-webkit-input-placeholder {
                color: #adb5c0;
                font-size: 14px;
            }

        }
        button{
          width: 225px;
          margin-left: 368px;
          background: #3051ae;
          color: 14px;
        }
        label {
            color: #8193b9;
            font-size: 14px;
            text-align: right;
        }
    }
    .billManage-message{
        background: red;
        thead{
          font-size:14px;
          .cell{
            color: #8193b9;
          }
        }
        tbody{
          color: #4a4a4a;
          tr{
            font-size:12px;
            .see{
              color:#376fd0;
            }
          }
        }
    }
}
</style>



